export const Paatos = ({type,kokous_id,kohta_id,tila,paatos,save}) => {
    var thisdata =""
    const [nappiBool, setnappibool] = useState(false) 
    thisdata = paatos;
    const this_save = (_id,data,delay) => {
        thisdata = data;
        save(kohta_id,[data,2],delay)
      } 
    
    const naytavalinnat = () => {
        
        setnappibool(!nappiBool);
    }

    return (
        <div>
        <div className={"paatos"+tila}>
            <div className="nuija" onClick={()=>naytavalinnat()}><div className="nuija2"><FaGavel/></div> </div>
            <div className="paatos_editori"><ResizeTextArea edit={tila!=3} id={kohta_id} sisus={paatos} save={this_save}/></div>
          
            {nappiBool?
            (tila==3)?<div  className="paatos_valinnat"><div className="avaa" onClick={()=>{setnappibool(false);save(kohta_id,[paatos,2],100)}}>AVAA</div></div>:
            <div  className="paatos_valinnat"><div onClick={()=>{setnappibool(false);save(kohta_id,[thisdata,3],100)}}>Hyvksy!</div><div>Lisaikaa</div><div>Hylk</div></div>:""}
            
          
           {
           /* 
            <div>
            ni on annettu thn menness.... nestys aikaa jljell viel 2 piv.
            </div>
        
            <div>
            Ei tarpeeksi ni, valintaa lyktn 5 pivll.
            </div>
        
            <div>
            nestyksess on tasapeli tilanne
            </div>
        
            <div>
            Olet saanut eniten ni puheenjohtaja valinnassa.
            
                <div> Hyvksyn </div> 
                <div> En halua </div> 
                <div className="teepaatos">
                
                tee pts</div>
           </div>*/
           }
        </div>
       
        </div>
    )
}

export default Paatos